<!--suppress ALL -->
<template>
  <el-container id="components-dashboard-component">
    <!--Aside Filter-->
    <el-aside class="aside" :class="{ 'aside-active': hasAside }">
      <div class="search-container">
        <el-input
          class="search-input"
          v-model="searchContent"
          placeholder="Search Fabric"
          clearable
        ></el-input>
        <div class="hide-button-container" @click="onHideButtonClick(false)">
          <p>Hide</p>
          <img
            class="hide-icon"
            src="@/assets/images/icons/png/switch.png"
            alt=""
          />
        </div>
      </div>
      <template v-for="(option, index) in filterOptions" :key="index">
        <CollapseItem v-if="option.items.length" :title="option.title">
          <template v-if="option.title === 'Colors'">
            <el-row class="colors-container" :gutter="5">
              <el-col
                class="color-wrapper"
                v-for="(item, _index) in option.items"
                :key="_index"
                :span="4"
                @click="item.checked = !item.checked"
              >
                <div
                  class="color-circle"
                  :class="{ 'color-active': item.checked }"
                  :style="{ background: item.label }"
                ></div>
              </el-col>
            </el-row>
          </template>
          <template v-else>
            <div
              v-for="(item, _index) in option.items"
              :key="_index"
              class="option"
            >
              <input type="checkbox" v-model="item.checked" />
              <div class="name">{{ item.label }}</div>
            </div>
          </template>
        </CollapseItem>
      </template>

      <CollapseItem
        v-for="(option, index) in sliderOptions"
        :key="index"
        :title="option.title"
      >
        <el-slider
          :value="option.value"
          :min="option.range.min"
          :max="option.range.max"
          :marks="option.marks"
          range
          @change="e => onSliderChange(e, option.title)"
        >
        </el-slider>
      </CollapseItem>
    </el-aside>

    <div
      v-if="hasOpenButton"
      class="open-aside-button"
      @click="onHideButtonClick(true)"
    >
      <img
        class="arrow-right-icon"
        src="@/assets/images/icons/png/arrow-right.png"
        alt=""
      />
    </div>

    <!--Fabric Cards-->
    <el-main class="main-container">
      <div class="console-container">
        <el-button
          v-permission="'fabric_analysis_view'"
          type="info"
          @click="onAnalysisClick"
        >
          Analysis
        </el-button>
        <el-button
          v-permission="'fabric_upload'"
          type="info"
          @click="onUploadClick"
        >
          Upload
        </el-button>
      </div>
      <div v-if="tags.length" class="tags-container">
        <FilterTag
          v-for="(tag, index) in tags"
          :key="index"
          :data="tag"
          :closable="true"
          @close="onTagCloseClick"
        ></FilterTag>
        <p class="button-clear-all-tags" @click="onCloseAllTags">Clear All</p>
      </div>
      <el-row :gutter="30">
        <el-col
          class="col"
          v-for="(item, index) in list"
          :key="index"
          :span="hasAside ? 8 : 6"
        >
          <FabricCard
            :data="item"
            has-buttons
            :status="item.hover"
            @add-to-favorite="onFabricCardAddToFavorite"
            @add="onCardAddClick"
          ></FabricCard>
        </el-col>
      </el-row>
      <el-pagination
        v-if="pageCount"
        background
        layout="prev, pager, next"
        :current-page="page"
        :page-count="pageCount"
        @current-change="onPaginationChange"
      >
      </el-pagination>
    </el-main>

    <!--For Fabric Card Editable Component-->
    <AddToCollectionDialog ref="addToCollectionDialog"></AddToCollectionDialog>
  </el-container>
</template>

<script lang="ts">
import axios from "axios";
import { defineComponent, ref, computed, watch, onMounted, Ref } from "vue";
import { useStore } from "vuex";
import { FilterOption, SliderOption } from "@/typings/asideFilter";
import {
  GetFabricFilters,
  GetFabrics,
  PostAddFabricToFavorite,
  SetFabricList
} from "@/typings/store";

import { ElNotification } from "element-plus";
import { loading } from "@/utils";
import FilterTag from "@/components/FilterTag.vue";
import FabricCard from "@/components/FabricCard.vue";
import CollapseItem from "@/components/CollapseItem.vue";
import AddToCollectionDialog from "@/components/AddToCollectionDialog.vue";

import {
  closeAllTags,
  closeTag,
  createFilterOptions,
  createSliderOptions,
  watchFilterOptions
} from "@/utils/asidtFilter";
import { FabricResult, FabricsParams } from "@/typings/fabric";
import { CollectionAddFabric } from "@/typings/collection";
import { iCreateDialog } from "@/typings/component";
import { useRouter } from "vue-router";

const createFabricsParams = (options: FilterOption[]): FabricsParams => {
  const _options = options.reduce((target: any, option: FilterOption) => {
    target[option.title] = option.items
      .filter(item => item.checked)
      .reduce((str, item, index) => {
        if (index === 0) {
          return str + `${item.value}`;
        }
        return str + `,${item.value}`;
      }, "");
    return target;
  }, {});

  const params: FabricsParams = {};
  if (_options["Real Composition"])
    params.real_composition = _options["Real Composition"];
  if (_options["Name"]) params.name = _options["Name"];
  // if (_options["Finished Specification"])
  //   params.finished_specification = _options["Finished Specification"];
  if (_options["Colors"]) params.colors = _options["Colors"];
  if (_options["Status"]) params.status = _options["Status"];
  return params;
};

const createSliderParams = (options: SliderOption[]) => {
  const _options: any = {};
  options.forEach(item => {
    if (item.title === "Width") {
      _options["width_1_min"] = item.value[0];
      _options["width_1_max"] = item.value[1];
    }
    if (item.title === "Weight") {
      _options["weight_m2_min"] = item.value[0];
      _options["weight_m2_max"] = item.value[1];
    }
  });
  return _options;
};

const Component = defineComponent({
  name: "Fabrics",
  components: {
    FilterTag,
    FabricCard,
    CollapseItem,
    AddToCollectionDialog
  },
  setup() {
    const store = useStore();
    const router = useRouter();

    const addToCollectionDialog: Ref<iCreateDialog | null> = ref(null);
    const tags = ref<string[]>([]);
    const hasAside = ref(true);
    const hasOpenButton = ref(false);
    const searchContent = ref("");
    const page = ref(1);
    const pageCount = ref(0);

    const list = computed(() => store.state.fabric.list);

    const filterOptions = ref<FilterOption[]>([]);
    const sliderOptions = ref<SliderOption[]>([]);

    watch<(FilterOption[] | SliderOption[])[]>(
      [filterOptions, sliderOptions, searchContent],
      async ([fOptions, sOptions, q], [prevFOptions, prevSOption, prevQ]) => {
        if (!prevFOptions.length && prevSOption.length && !prevQ) return;
        tags.value = watchFilterOptions(fOptions as FilterOption[]);
        const params = {
          ...createFabricsParams(fOptions as FilterOption[]),
          ...createSliderParams(sOptions as SliderOption[]),
          q
        };

        const data = await store.dispatch(GetFabrics, params);
        page.value = data.page;
        pageCount.value = data.pageCount;
      },
      { deep: true }
    );

    onMounted(async () => {
      const loadingInstance = loading();
      try {
        await store.dispatch(GetFabricFilters);
        const data = await store.dispatch(GetFabrics, {});
        page.value = data.page;
        pageCount.value = data.pageCount;

        const filterData = store.state.fabric.filters;
        filterOptions.value = createFilterOptions(filterData, [
          { title: "Real Composition", key: "real_composition" },
          { title: "Name", key: "name" },
          // { title: "Finished Specification", key: "finished_specification" },
          { title: "Status", key: "status", default: "public" }
        ]);
        console.log(filterOptions);
        sliderOptions.value = createSliderOptions(filterData, [
          { title: "Weight", key: "weight_m2" },
          { title: "Width", key: "width_1" }
        ]);
      } finally {
        loadingInstance.close();
      }
    });

    const onHideButtonClick = (value: boolean) => {
      hasAside.value = value;

      if (!value) {
        setTimeout(() => {
          hasOpenButton.value = true;
        }, 300);
      } else {
        hasOpenButton.value = false;
      }
    };

    const onTagCloseClick = (tag: string) => {
      filterOptions.value = closeTag(filterOptions.value, tag);
    };

    const onCloseAllTags = () => {
      filterOptions.value = closeAllTags(filterOptions.value);
    };

    const onFabricCardAddToFavorite = async (id: number) => {
      const { status, message } = await store.dispatch(
        PostAddFabricToFavorite,
        id
      );
      if (status === "success") {
        store.commit(
          SetFabricList,
          list.value.map((it: FabricResult) => ({
            ...it,
            favorite: it.id === id ? message.includes("add") : it.favorite
          }))
        );
      } else {
        ElNotification({
          title: "Error",
          type: "error",
          message
        });
      }
    };

    const onCardAddClick = (data: CollectionAddFabric) => {
      if (!addToCollectionDialog.value) return;
      addToCollectionDialog.value.open(data);
    };

    const onSliderChange = (range: number[], title: string) => {
      const target = sliderOptions.value.find(it => it.title === title);
      if (target) {
        target.value = range;
      }
    };

    const onPaginationChange = async (_page: number) => {
      const params = createFabricsParams(filterOptions.value);
      const data = await store.dispatch(GetFabrics, { ...params, page: _page });
      page.value = data.page;
      pageCount.value = data.pageCount;
    };

    const onUploadClick = async () => {
      await router.push({ name: "FabricUpload" });
    };

    const onAnalysisClick = async () => {
      await router.push({ name: "FabricsAnalysis" });
    };

    return {
      addToCollectionDialog,
      list,
      page,
      pageCount,
      tags,
      filterOptions,
      sliderOptions,
      hasAside,
      hasOpenButton,
      searchContent,

      onHideButtonClick,
      onTagCloseClick,
      onCloseAllTags,
      onFabricCardAddToFavorite,
      onCardAddClick,
      onPaginationChange,
      onSliderChange,
      onUploadClick,
      onAnalysisClick
    };
  }
});

export default Component;
</script>

