
import { defineComponent, ref } from "vue";

const Component = defineComponent({
  name: "CollapseItem",
  props: {
    title: {
      type: String,
      default: ""
    }
  },
  setup() {
    const isOpen = ref(false);

    const onTitleClick = () => {
      isOpen.value = !isOpen.value;
    };

    return {
      isOpen,

      onTitleClick
    };
  }
});

export default Component;
