<template>
  <el-tag
    id="filter-tag-component"
    type="success"
    size="medium"
    :closable="closable"
    :disable-transitions="false"
    @close="onCloseTag"
  >
    {{ data }}
  </el-tag>
</template>

<script lang="ts">
import { defineComponent, toRefs } from "vue";

const Component = defineComponent({
  name: "FilterTag",
  props: {
    data: {
      type: String,
      default: ""
    },
    closable: {
      type: Boolean,
      default: false
    }
  },
  setup(props, context) {
    const { data } = toRefs(props);

    const onCloseTag = () => {
      context.emit("close", data.value);
    };

    return {
      onCloseTag
    };
  }
});

export default Component;
</script>

<style scoped lang="stylus">
#filter-tag-component
  margin 0 15px 10px 0
  background $color-tag-background
  color $color-tag-text
  padding 0 15px
