
import { defineComponent, toRefs } from "vue";

const Component = defineComponent({
  name: "FilterTag",
  props: {
    data: {
      type: String,
      default: ""
    },
    closable: {
      type: Boolean,
      default: false
    }
  },
  setup(props, context) {
    const { data } = toRefs(props);

    const onCloseTag = () => {
      context.emit("close", data.value);
    };

    return {
      onCloseTag
    };
  }
});

export default Component;
