<template>
  <div id="collapse-item-component">
    <div class="title-container" @click="onTitleClick">
      <h3 class="title">{{ title }}</h3>
      <img
        class="arrow-icon"
        src="@/assets/images/icons/png/arrow-down.png"
        alt=""
      />
    </div>
    <el-collapse-transition>
      <div class="slot-container" v-show="isOpen">
        <slot></slot>
      </div>
    </el-collapse-transition>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from "vue";

const Component = defineComponent({
  name: "CollapseItem",
  props: {
    title: {
      type: String,
      default: ""
    }
  },
  setup() {
    const isOpen = ref(false);

    const onTitleClick = () => {
      isOpen.value = !isOpen.value;
    };

    return {
      isOpen,

      onTitleClick
    };
  }
});

export default Component;
</script>

<style scoped lang="stylus">
#collapse-item-component
  padding 0 5px
  border-bottom solid 1px $color-secondary
  .title-container
    display flex
    justify-content space-between
    align-items center
    padding 15px 0
    cursor pointer
    .title
      font-size 16px
      font-weight 600
    .arrow-icon
      width auto
      height 10px

  .slot-container
    padding 0 0 10px
</style>
